$gray250: rgb(250,250,250);
$gray240: rgb(240,240,240);
$gray230: rgb(230,230,230);
$gray220: rgb(220,220,220);
$gray210: rgb(210,210,210);
$gray200: rgb(200,200,200);
$gray180: rgb(180,180,180);
$gray150: rgb(150,150,150);
$gray120: rgb(120,120,120);
$gray90: rgb(90,90,90);
$gray60: rgb(60,60,60);
$gray50: rgb(50,50,50);
$gray40: rgb(40,40,40);
$gray30: rgb(30,30,30);

$purple: rgb(171,71,188);
$blue: rgb(3,169,244);
$green: rgb(76,175,80);
$red: rgb(211,47,47);
$orange: rgb(255,111,0);
$yellow: rgb(251,192,45);

$shadow1: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
$shadow2: rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px;
$shadow3: rgba(0, 0, 0, 0.19) 0px 10px 30px, rgba(0, 0, 0, 0.23) 0px 6px 10px;

@mixin center{
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

html, body{
  font-family: 'Roboto', sans-serif;
  height: 100%;
  margin: 0px;
  overflow: hidden;
  padding: 0px;
  width: 100%;
}

.invisible{
  opacity: 0;
}

.scroll-bar{
  &::-webkit-scrollbar {
    background-color: $gray200;
    width: 2px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $gray90;
  }
}

// Form di login iniziale
.login-form {
  @include center;
  background: white;
  border-radius: 20px;
  box-shadow: $shadow2;
  padding: 40px;
  text-align: center;
  width: 400px;
  
  h1 {
    color: $gray60;
    font-weight: 300;
    margin-bottom: 30px;
  }
  
  input {
    border: 1px solid $gray210;
    border-radius: 25px;
    color: $gray60;
    font-size: 1em;
    margin-bottom: 20px;
    outline: none;
    padding: 12px 20px;
    width: calc(100% - 40px);
    
    &:focus {
      border-color: $blue;
    }
  }
  
  button {
    background: linear-gradient(to right, $purple, $blue);
    border: none;
    border-radius: 25px;
    color: white;
    cursor: pointer;
    font-size: 1em;
    padding: 12px 40px;
    transition: transform 0.3s;
    
    &:hover {
      transform: scale(1.05);
    }
  }
}

#chat-wrapper{
  background-color: white;
  height: 100%;
  overflow: hidden;
  width: 100%;
  
  #chat-peer-info{
    left: 0px;
    padding: 20px;
    position: absolute;
    top: 0px;
    white-space: nowrap;
    z-index: 10;
    
    &.connected{
      #chat-peer-icon{
        background: linear-gradient(to right, $blue, $green);
      }
      
      #chat-peer-label{
        #chat-peer-status{
          color: $green;
        }
      }
    }
    
    &.waiting{
      #chat-peer-icon{
        background: linear-gradient(to right, $orange, $yellow);
      }
      
      #chat-peer-label{
        #chat-peer-status{
          color: $orange;
        }
      }
    }
    
    #chat-peer-icon{
      border-radius: 30px;
      box-shadow: $shadow1;
      display: inline-block;
      height: 30px;
      line-height: 30px;
      margin-left: 10px;
      text-align: center;
      vertical-align: top;
      width: 30px;
      
      i {
        color: white;
        font-size: 1em;
      }
    }
    
    #chat-peer-label{
      background-color: white;
      border-radius: 20px;
      box-shadow: $shadow1;
      display: inline-block;
      height: 20px;
      margin-left: 5px;
      padding: 5px 15px;
      vertical-align: top;
      
      h1{
        display: inline-block;
        font-size: 0.9em;
        font-weight: 300;
        height: 20px;
        line-height: 20px;
        margin: 0px;
        vertical-align: top;
      }
      
      #chat-peer-text{
        color: $gray60;
      }
      
      #chat-peer-value {
        margin-left: 5px;
      }
    }
  }
  
  #chat-message-window{
    height: 100%;
    left: 0px;
    min-width: 600px;
    overflow: hidden;
    position: absolute;
    top: 0px;
    width: 100%;
    z-index: 2;
    
    #message-input-wrapper{
      bottom: 0px;
      height: 90px;
      left: 0px;
      position: absolute;
      width: 100%;
      z-index: 2;
      
      #message-input{
        @include center;
        background-color: white;
        border-radius: 100px;
        box-shadow: $shadow1;
        height: 50px;
        line-height: 50px;
        overflow: hidden;
        width: 500px;
          
        &.send-enabled{
          #send-message-button{
            &:before, &:after{
              @include center;
              animation: pulse 2s ease-in-out infinite;
              border-radius: 100px;
              content: '';
              height: 30px;
              width: 30px;
            }
            
            i{
              color: $blue;
            }
          }
        }
        
        #message-input-field{
          border: none;
          color: $gray180;
          font-size: 1em;
          font-weight: 300;
          margin-left: 20px;
          outline: none;
          width: 430px;
          
          &::placeholder{
            color: $gray180;
          }
        }

        #send-message-button{
          cursor: pointer;
          height: 50px;
          position: absolute;
          right: 0px;
          text-align: center;
          top: 0px;
          width: 50px;

          i{
            color: $gray230;
            height: 50px;
            line-height: 50px;
            font-size: 2em;
            transition: all 0.5s;
            width: 50px;
          }
        }
      }
    }
     
    #chat-message-column-wrapper{
      bottom: 0px;
      height: 100%;
      left: 0px;
      overflow-y: auto;
      position: absolute;
      width: 100%;
      z-index: 1;
      
      #chat-message-column{
        bottom: 0px;
        left: 0px;
        min-height: 70px;
        margin-bottom: 70px;
        overflow: hidden;
        padding: 80px 20px 0px 20px;
        width: calc(100% - 40px);
        
        &.static{
          position: absolute;
        }
        
        .message{
          animation: expand 0.5s ease-in-out;
          margin-bottom: 20px;
          white-space: nowrap;
          
          &.received{
            text-align: left;
            
            .profile-icon{
              background: linear-gradient(to right, $blue, $green);
              
              i{
                font-size: 1.5em;
              }
            }
            
            .content{
              animation: bounceInLeft 1s ease-in-out;
              margin-left: 20px;
            }
          }
          
          &.sent{
            text-align: right;
            
            .profile-icon{
              background: linear-gradient(to right, $purple, $blue);
              
              i{
                font-size: 1.25em;
              }
            }
            
            .content{
              animation: bounceInRight 1s ease-in-out;
              margin-right: 20px;
            }
          }
          
          .profile-icon{
            animation: bounceIn 1s ease-in-out;
            border-radius: 100px;
            box-shadow: $shadow1;
            display: inline-block;
            height: 50px;
            position: relative;
            vertical-align: top;
            width: 50px;
            z-index: 2;
            
            i{
              color: white;
              height: 50px;
              line-height: 50px;
              text-align: center;
              width: 50px;
            }
          }
          
          .content{
            background-color: white;
            border-radius: 25px;
            box-shadow: $shadow1;
            display: inline-block;
            max-width: 300px;
            padding: 15px 20px;
            position: relative;
            text-align: left;
            transition: all 0.5s;
            vertical-align: top;
            white-space: normal;
            z-index: 1;
            
            .text{
              color: $gray180;
              font-size: 1em;
              font-weight: 300;
              margin: 0px;
              min-height: 20px;
              transition: all 0.5s;
              word-wrap: break-word;
            }
          }
        }
      }
    }
  }
}

// Animazioni
@keyframes expand{
  from{ max-height: 0px; }
  to{ max-height: 200px; }
}

@keyframes bounceIn {
  from, 20%, 40%, 60%, 80%, to {
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }
  0% {
    opacity: 0;
    transform: scale3d(.3, .3, .3);
  }
  20% { transform: scale3d(1.1, 1.1, 1.1); }
  40% { transform: scale3d(.9, .9, .9); }
  60% {
    opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03);
  }
  80% { transform: scale3d(.97, .97, .97); }
  to {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}

@keyframes bounceInLeft {
  from, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }
  0% {
    opacity: 0;
    transform: translate3d(-3000px, 0, 0);
  }
  60% {
    opacity: 1;
    transform: translate3d(25px, 0, 0);
  }
  75% { transform: translate3d(-10px, 0, 0); }
  90% { transform: translate3d(5px, 0, 0); }
  to { transform: none; }
}

@keyframes bounceInRight {
  from, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }
  from {
    opacity: 0;
    transform: translate3d(3000px, 0, 0);
  }
  60% {
    opacity: 1;
    transform: translate3d(-25px, 0, 0);
  }
  75% { transform: translate3d(10px, 0, 0); }
  90% { transform: translate3d(-5px, 0, 0); }
  to { transform: none; }
}

@keyframes pulse {
  0% {
    box-shadow: 0px 0px 0px 0px rgba(3, 169, 244, .5);
  }
  100% {
    box-shadow: 0px 0px 0px 20px rgba(3, 169, 244, 0);
  }
}